<template>
  <div class="finance_left">
    <img :src="this.user.avater" class="avatar" />
    <div style="height: 180px">
      <h3 style="height: 50px; line-height: 200px; font-weight: 400">
        {{ this.user.nickname }}
      </h3>
    </div>
    <div>
      <p style="font-size: 20px">账户余额</p>
      <p style="margin: 20px 0; color: #ffcd42; font-size: 18px">
        ￥ {{ this.user.balance }}
      </p>
      <el-button
        type="primary"
        style="margin-top: 20px; width: 70%"
        @click="dialogVisible = true"
        >点击充值</el-button
      >
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <el-input
        placeholder="请输入充值金额"
        label="金额"
        style="width: 70%"
        v-model="money"
      />
      <span slot="footer" class="dialog-footer">
        <el-button @click="recharge" type="primary">确定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "FinanceLeft",

  data() {
    return {
      user: this.$store.getters.getUser,
      dialogVisible: false,
      money: "",
    };
  },

  created() {
    this.getUser();
  },

  mounted() {},

  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
    recharge() {
      if (this.money == "") {
        this.$message.error("请输入充值金额");
        return;
      }
      this.money = parseFloat(this.money).toFixed(2);
      this.$axios
        .get("/buy", {
          params: { money: this.money, id: this.user.id },
        })
        .then((res) => {
          if (res.data.code == 200) {
            window.location.href = res.data.data;
            // window.open(res.data.data, "_blank");
            this.$message.success("请至新窗口完成支付");
            this.dialogVisible = false;
          } else {
            this.$message.error(res.data.msg);
          }
        });
    },
    getUser() {
      this.$axios
        .get("/getUserInfo", {
          params: {
            id: this.user.id,
          },
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.user = res.data.data;
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.finance_left {
  position: relative;
  margin-top: 45px;
  width: 30%;
  height: 75vh;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}
.avatar {
  position: absolute;
  left: 37%;
  top: -8%;
  width: 100px;
  height: 100px;
  display: block;
  border-radius: 50%;
  box-shadow: 0px 0px 15px rgba(153, 224, 255, 0.886);
}
</style>