<template>
  <div class="finance_right">
    <template>
      <el-table :data="tableData" style="width: 100%" :stripe="true">
        <el-table-column
          prop="orderNumber"
          label="流水号"
          width="180"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="orderTime"
          label="时间"
          width="200"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="orderMoney"
          label="金额"
          width="120"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="type"
          label="收支类型"
          width="160"
          align="center"
        >
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  name: "FinanceRight",

  data() {
    return {
      tableData: [],
    };
  },

  mounted() {},

  created() {
    this.getOrder();
  },

  methods: {
    getOrder() {
      this.$axios
        .get("/getOrder", {
          params: {
            id: this.$store.getters.getUser.id,
          },
        })
        .then((res) => {
          for (var i = 0; i < res.data.data.length; i++) {
            res.data.data[i].orderTime = this.$moment(
              res.data.data[i].orderTime
            ).format("YYYY-MM-DD HH:mm:ss");
          }
          this.tableData = res.data.data;
        });
    },
  },
};
</script>

<style lang="less" scoped>
.finance_right {
  min-height: 80vh;
}
/deep/.el-table {
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
</style>